.answer{
    padding: 10px;
    border-bottom: solid 1px lightgray;
    margin: 10px 100px;
    display: flex;
    flex-direction: column;
    height: inherit;
}
.answer.hidden{
    height: 0px;
    margin: 0px;
    padding: 0px;
    border: none;
    opacity: 0;
}
.question{
    color: #9b9696;
}
.solution{
    font-size: 1.5em;
}