.input{
    width: 1000px;
    height: 35px;
    margin: 20px;
    border-radius: 20px;
    text-align: center;
    font-size: 1.8em;
}
.search
{
    position: sticky;
    display: flex;
    top: 20px;
    margin: 0 150px;
    justify-content: center;
}
.relative{
    position: relative;
    width: fit-content;    
    display: flex;
    align-content: center;
    justify-content: center;
}
.cetification-choice{
    display: flex;
    align-items: flex-start;
    flex-direction: column;    
    margin: 15px;
    z-index: 1000;
    position: absolute;
    top: 20px;
}
.cetification-choice button{
    margin: 5px;
    min-width: 130px;
    padding:  10px 45px;
    background: black;
}
.delete-search{
    position: absolute;
}